<template>
  <div class="menu">
    <div class="menu-icon" :class="{'active': openMenu}" @click.stop="openMenu = !openMenu">
      <div></div>
    </div>
    <transition
      appear
      :enter-active-class="enterActiveClass"
      :leave-active-class="leaveActiveClass"
    >
      <div v-show="openMenu" class="menu-container">
        <div class="head text-center">
          <img class="m-auto" alt="tms-pro logo" src="/img/icons/apple-touch-icon-120x120.png">
        </div>
        <div class="menu-items-container" @click="openMenu = false">
          <router-link tag="div" :to="{name: 'home'}" class="menu-item py-2 px-2 pl-3">
            <div class="menu-item-text">Accueil</div>
          </router-link>
          <router-link tag="div" :to="{name: 'services'}" class="menu-item py-2 px-2 pl-3">
            <div class="menu-item-text">Nos services</div>
          </router-link>
          <router-link tag="div" :to="{name: 'contact'}" class="menu-item py-2 px-2 pl-3">
            <div class="menu-item-text">Contactez-nous</div>
          </router-link>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
export default {

  data () {
    return {
      openMenu: false,
      screenWidth: 0
    }
  },

  computed: {
    enterActiveClass () {
      if (this.screenWidth >= 760) return 'animated fadeInDown'
      else return 'animated fadeInLeft'
    },

    leaveActiveClass () {
      if (this.screenWidth >= 760) return 'animated fadeOutUp'
      else return 'animated fadeOutLeft'
    }
  },

  mounted () {
    this.screenWidth = window.innerWidth
    window.onresize = () => this.screenWidth = window.innerWidth

    const close = event => {
      event = event || window.event;
      if (event.keyCode == 27) this.openMenu = false
    }

    document.addEventListener('keydown', close)
  },
}
</script>

<style lang="scss">
.menu {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  z-index: 200;

  .menu-icon {
    position: absolute;
    top: 0;
    left: 10px;
    z-index: 201;
    margin: 1em;
    width: 30px;
    cursor: pointer;

    &:after,
    &:before,
    div {
      background-color: rgb(0, 0, 0);
      border-radius: 3px;
      content: "";
      display: block;
      height: 2px;
      margin: 7px 0;
      transition: all 0.2s ease-in-out;
    }

    &.active {
      &:before {
        transform: translateY(12px) rotate(135deg);
      }

      &:after {
        transform: translateY(-12px) rotate(-135deg);
      }

      div {
        transform: scale(0);
      }
    }
  }

  .menu-container {
    background-color: #fff;
    top: 0;
    left: 0;
    @media (max-width: 760px) {
      position: absolute;
      width: 250px;
      height: 100%;
      box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);

      .head {
        padding-top: 2em;
        padding-bottom: 2em;
      }

      .menu-items-container {
        .menu-item {
          cursor: pointer;
          color: #000;
          &:hover {
            background-color: rgba(232, 233, 235, 0.24);
          }

          &.active,
          &.router-link-active {
            color: #007bff;
            border-right: 1px solid #007bff;
            background-color: rgba(232, 233, 235, 0.24);
          }
        }
      }
    }

    @media (min-width: 760px) {
      position: fixed;
      width: 100%;
      box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075);
      // box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
      background-color: rgb(241, 241, 241);

      .head {
        padding-top: 1em;
        padding-bottom: 1em;
      }

      .menu-items-container {
        border-top: 1px solid rgb(241, 241, 241);
        background-color: #fff;
        display: flex;
        align-items: center;
        .menu-item {
          cursor: pointer;
          color: #000;
          &:hover {
            background-color: rgba(232, 233, 235, 0.24);
          }

          &.active,
          &.router-link-active {
            color: #007bff;
            border-bottom: 1px solid #007bff;
            border-right: 0;
            background-color: rgba(232, 233, 235, 0);
          }

          &:first-child {
            margin-left: auto;
          }
          &:last-child {
            margin-right: auto;
          }
        }
      }
    }
  }
}
</style>
