<style lang="scss">
.fiche-container {
  display: inline-grid;
  grid-gap: 20px;

  @media (min-width: 992px) {
    grid-template-columns: repeat(3, 1fr);
  }
  @media (max-width: 992px) {
    grid-template-columns: repeat(2, 1fr);
  }

  @media (max-width: 560px) {
    grid-template-columns: repeat(1, 1fr);
  }
}
.fiche {
  &.fiche-sm {
    width: 250px;
    transition: all 0.5s ease;
    overflow: hidden;
    background-color: #fff;

    &:hover {
      box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
    }

    .fiche-img-container {
      max-height: 180px;
      overflow: hidden;
      text-align: center;

      .fiche-img {
        width: 100%;
      }
    }
  }

  &.fiche-lg {
    .head {
      position: sticky;
      top: 0;
      box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075);
      padding-left: 5em;
      padding-right: 22%;
    }
    .fiche-img-container {
      text-align: center;
      .fiche-img {
        width: 100%;
      }
    }

    .btn-commande {
      position: fixed;
      right: 2em;
      bottom: 2em;
    }
  }
}
</style>

<template>
  <div class="servive-container">
    <div class="container spinner-container">
      <div class="row justify-content-center">
        <!-- <div class="col-lg-8 text-center"> -->
          <div class="animated fadeInLeft spinner mt-5">
            <div
              class="spinner-item px-2 small"
              :class="{'active': slide == 'website'}"
              @click="slide = 'website'"
            >sites web</div>
            <!-- <div
              class="spinner-item px-2 small"
              :class="{'active': slide == 'logiciel'}"
              @click="slide = 'logiciel'"
            >Logiciels</div> -->
          </div>
        <!-- </div> -->
      </div>
    </div>

    <transition-group
      appear
      enter-active-class="animated fadeInLeft"
      leave-active-class="animated fadeOutRight"
      mode="out-in"
    >
      <div v-show="slide == 'website'" class="slide" :key="1">
        <div class="container mt-5 pt-5">
          <div class="row justify-content-center">
            <div class="col-lg-10">
              <div class="h4 lobster mt-5">Créer votre site web</div>
              <p>
                Pour vous nous allons créer le
                <strong class="text-primary">meilleur site internet</strong> qui correspond parfaitement à
                votre activité. Choisissez parmi
                <strong
                  class="text-primary"
                >des dizaines de templates</strong> et ensemble nous le peaufinons
                à votre goût. Si vous avez une idée en tête prenez la planche et commencez à dessiner quelque
                soit sa forme nous le réaliserons pour vous.
              </p>

              <button class="btn btn-primary rounded-pill btn-sm">Commander mon site</button>
            </div>

            <div class="col-lg-10 my-5">
              <div class="fiche-container">
                <div
                  v-for="(template, t) in templates"
                  :key="t"
                  class="fiche fiche-sm rounded border"
                >
                  <div class="fiche-img-container">
                    <img class="fiche-img" :src="template.img" alt>
                  </div>
                  <div class="p-3">
                    <div class="h5 m-0">{{template.title}}</div>
                    <p>{{template.description}}</p>
                    <button class="btn btn-sm btn-primary mb-2 ml-2 rounded-pill">Commander</button>

                    <router-link
                      :to="{name: 'template', params: {template: template}}"
                      tag="button"
                      class="btn btn-sm btn-light mb-2 rounded-pill ml-2"
                    >apperçu</router-link>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div v-show="slide == 'logiciel'" class="slide" :key="2">
        <div class="container mt-5 pt-5">
          <div class="row justify-content-center">
            <div class="col-lg-8">
              <div class="h4 lobster mt-5">Commandez votre logiciel sur mesure</div>
              <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Odio iste dolorum inventore, neque optio accusantium eaque maxime! Eligendi qui iste at ratione fugiat nisi, sequi nemo error, reiciendis laudantium unde!</p>

              <button class="btn btn-primary rounded-pill">Je commande</button>
            </div>
          </div>
        </div>
      </div>
    </transition-group>
  </div>
</template>

